.App {
  text-align: center;
  background-color: 080808;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
}

.App-header {
  background-color: #080808;
  padding: 20px;
  display: flex;
  flex-direction: column;
  height: 120px;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App-footer {
  background-color: #080808;
  padding: 10px;
  text-align: center;
  color: white;
}

.image-container {
  position: relative;
  display: inline-block;
}

.hover-white:hover {
  color: white;
}

.playtime-overlay {
  position: absolute;
  background-color: black;
  color: white;
  padding: 0.2rem;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
  z-index: 1;
  bottom: -1rem;
  left: 50%;
  transform: translateX(-50%);
  white-space: nowrap;
}

.glow-on-hover {
  transition: transform 0.2s;
}

.glow-on-hover:hover {
  box-shadow: 0 0 10px #fff;
  border-radius: 50%;
  transform: scale(1.1);
}

.image-container:hover .playtime-overlay {
  visibility: visible;
  opacity: 1;
}

.hidden-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: -1;
  display: none;
}

.send-button {
  background-color: black !important;
  color: white !important;
}
